import { TabPane } from 'reactstrap'

import { Link } from 'react-router-dom'

const TabPaneUnidades = ({ unidad }) => {
  const tabId = `pills${unidad}`
  let desc, title

  switch (unidad) {
    case 'familia':
      title = 'Familia'
      desc =
        'Un Cachorro es un nio o nia entre 5 y 7 aos que se une al Movimiento Scout. Esta unidad fomenta su desarrollo personal a travs del aprendizaje activo, como aprender jugando, dentro del marco simblico de la fantasa en la familia. Raksha los gua y protege mientras exploran la selva del Seeonee.'
      break
    case 'manada':
      title = 'Manada'
      desc =
        'Un Lobato es un nio o nia de 7 a 10 aos, proveniente de la Familia de Cachorros o externo al Movimiento Scout. Sus actividades se desarrollan en la fantasa de la Manada, donde Akela los gua y acompaa en su recorrido por la selva del Seeonee.'
      break
    case 'aldea':
      title = 'Aldea'
      desc =
        'Un Webelo es un nio o nia que puede venir de la Manada de Lobatos o de fuera del Movimiento Scout entre los 10 y los 12 aos. Desarrolla sus actividades teniendo como marco simblico la exploracin en el bosque.'
      break
    case 'scouts':
      title = 'Scouts'
      desc =
        'Un Scout es un joven de 12 a 15 aos que, con espritu aventurero y soador, aprende de la naturaleza y vive desafos emocionantes y seguros que lo llevan a encontrar lo til de ella dentro del marco simblico de la Tropa.'
      break
    case 'sociedad':
      title = 'Sociedad'
      desc =
        'Un Pionero es un joven de 15 a 18 aos, proveniente de la Tropa Scout o externo al Movimiento Scout. Aventurero y soador, aprende de la naturaleza al desafiarse y descubrir su utilidad, disfrutando experiencias seguras dentro del marco simblico de la aventura real o cooperativa en la Sociedad.'
      break
    case 'clan':
      title = 'Clan'
      desc =
        'Un Rover es un joven adulto de 18 a 22 aos, proveniente de la Sociedad de Pioneros o externo al Movimiento Scout. Trabajan en el Clan, una unidad donde, unidos por lazos de afecto, actan para transformar positivamente sus entornos y ser protagonistas en sus comunidades. La propuesta Rover est abierta a quienes deseen vivir los valores de la Promesa y la Ley, con la libertad como marco simblico.'
      break
    default:
      break
  }

  return (
    <TabPane tabId={tabId} className='px-md-5 mx-md-5'>
      <h3 className='title text-center'>
        <Link to={`/${unidad}`} className='text-info'>
          {title}
        </Link>
      </h3>
      <p>
        {desc}
        <br />
        <br />
        <Link to={`/${unidad}`} className='text-info'>
          Ver ms
        </Link>
      </p>
      <br />
    </TabPane>
  )
}

export default TabPaneUnidades
